<template>
  <div class="homePage">
    <el-container class="loadingtext">
      <!-- 头部 begin -->
      <el-header>
        <div class="top-header">
          <span class="top-breadCrumb">个人设置</span>
        </div>
      </el-header>
      <!-- 头部 end -->
      <div class="reminderSetting" style="padding: 0; background:#fff;"
           :style="{ 'height': bodyHeight - 97 + 'px'}">
        <el-row>
          <el-col>
            <el-tabs :tab-position="tabPosition" style="padding-left: 10px" :style="{ 'height': bodyHeight - 110 + 'px'}">
              <el-tab-pane label="提醒设置">
                <!--<p>{{ userName }}</p>-->
                <p>提醒设置</p>
                <el-checkbox v-model="todoEmailRealTimeReminder" label="接收待办提醒邮件"></el-checkbox>
                <el-checkbox v-model="todoEmailDailyReminder" label="接收每日待办提醒邮件"></el-checkbox>
                <el-checkbox v-model="processEndEmailReminder" label="接收流程结束提醒邮件"></el-checkbox>
                <span class="dialog-footer">
                  <div style="text-align: left; margin: 30px 0 0 80px">
                   <el-button type="primary" size="mini" @click="saveReminderSetting" class="agree">保 存</el-button>
                   <el-button type="primary" class="abolish" @click="$router.go(-1)" size="mini">取 消</el-button>
                  </div>
                </span>
              </el-tab-pane>
            </el-tabs>
          </el-col>
        </el-row>
      </div>
    </el-container>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex';
  import loading from '../../plugins/loading/loading';

  export default {
    name: "reminderSetting",
    data() {
      return {
        userName: localStorage.getItem('username'),//登录名
        "todoEmailRealTimeReminder": false,
        "todoEmailDailyReminder": false,
        "processEndEmailReminder": false,
        tabPosition: 'left'
      }
    },
    computed: {
      bodyHeight() {
        return document.documentElement.clientHeight
      },
      ...mapGetters(['bodyHeight'])
    },
    mounted() {
      this.reminderSetting();
    },
    methods: {
      // 提醒设置
      reminderSetting() {
        this.$axios.post('api/remindSetting', {}).then(res => {
          this.todoEmailRealTimeReminder = res.data.todoEmailRealTimeReminder == 1 || res.data.todoEmailRealTimeReminder == null;
          this.todoEmailDailyReminder = res.data.todoEmailDailyReminder == 1 || res.data.todoEmailDailyReminder == null;
          this.processEndEmailReminder = res.data.processEndEmailReminder == 1 || res.data.processEndEmailReminder == null;
        }).catch(err => {
          console.log(err);
        })
      },
      saveReminderSetting() {
        loading.startLoading();
        let obj = {
          "todoEmailRealTimeReminder": this.todoEmailRealTimeReminder ? 1 : 0,
          "todoEmailDailyReminder": this.todoEmailDailyReminder ? 1 : 0,
          "processEndEmailReminder": this.processEndEmailReminder ? 1 : 0
        };
        this.$axios.post('api/remindSettingChange', obj).then(res => {
          if (res.status === 200) {
            this.$message.success('保存成功');
          }
          loading.endLoading();
        }).catch(err => {
          console.log(err);
          loading.endLoading();
        })
      },
    }
  }
</script>

<style scoped lang="scss">
  .reminderSetting {
    .el-button--primary {
      background: #4598ff;
    }
    .el-button.el-button--mini {
      border-radius: 15px;
      padding: 5px 20px;
      i.el-icon-circle-close-outline, i.el-icon-circle-check-outline {
        display: none;
      }
    }
    .el-button--mini:nth-of-type(1) {
      border: none;
    }
    .el-button--mini:nth-of-type(2) {
      border: 1px solid #ee524e;
      background: #ffffff;
      color: #ee524e;
    }
    .el-button--mini:nth-of-type(1):hover {
      background: -webkit-linear-gradient(left, #3dd7fe, #3195f8); /* Safari 5.1 - 6.0 */
      background: -o-linear-gradient(right, #3dd7fe, #3195f8); /* Opera 11.1 - 12.0 */
      background: -moz-linear-gradient(right, #3dd7fe, #3195f8); /* Firefox 3.6 - 15 */
      background: linear-gradient(to right, #3dd7fe, #3195f8); /* 标准的语法 */
      color: #ffffff;
      border: none;
    }
    .el-button--mini:nth-of-type(2):hover {
      background: -webkit-linear-gradient(left, #fb8730, #ee524e); /* Safari 5.1 - 6.0 */
      background: -o-linear-gradient(right, #fb8730, #ee524e); /* Opera 11.1 - 12.0 */
      background: -moz-linear-gradient(right, #fb8730, #ee524e); /* Firefox 3.6 - 15 */
      background: linear-gradient(to right, #fb8730, #ee524e); /* 标准的语法 */
      color: #ffffff;
      border: 1px solid #ee524e;
    }
    .el-row {
      .el-col {
        .el-tabs {
          .el-tabs__content {
            .el-tab-pane {
              text-align: left;
              line-height: 40px;
              p {
                /*float: left;*/
              }
              .el-checkbox {
                display: block;
                margin-left: 150px;
              }
            }
          }
        }
      }
    }
  }
</style>



